/* BaseStyle.css */
/* @author: @rishisant */
/* @desc: Contains all styling used for HSS Application. */

/* All Font Imports */ /* Roboto Slab */

/* Background Image */
body {
    background-image: url(/static/media/subway_photo.c003aea1.png);
    background-size: cover;
    /* background-size: contain; */
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
}
/* Basic Styles */
.icon {
    width: 50px;
    height: 50px;
    margin: 0 auto;
    margin-bottom: 10px;
}

bt {
    font-family: 'Roboto Slab', serif;
    font-size: 20px;
    font-weight: 500;
    color: #000000;
}

ht {
    font-family: 'Roboto Slab', serif;
    font-size: 30px;
    font-weight: 500;
    color: #000000;
}

/* Home Screen Elements */

#mainlogo {
    display: flex;
    justify-content: center;
    width: 650px;
    margin: auto;
    -webkit-filter: drop-shadow(8px 8px 15px #cecdcd);
            filter: drop-shadow(8px 8px 15px #cecdcd);
    margin-top: 10%;
    -webkit-animation: pop 5s ease-in-out infinite;
            animation: pop 5s ease-in-out infinite;
    margin-bottom: 75px;
}

@-webkit-keyframes pop {
    0% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
}

@keyframes pop {
    0% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
}

.homebutton {
    font-family: Roboto Slab;
    outline: 8px double #F9F9F9;
    background-color: #6c1e1e;
    color: #ffffff;
    font-size: 20px;
    padding: 10px;
    border-radius: 20px;
    text-align: center;
    margin-left: 40%;
    margin-right: 40%;
    margin-bottom: 30px;
    -webkit-filter: drop-shadow(4px 4px 10px #cecdcd);
            filter: drop-shadow(4px 4px 10px #cecdcd);
    -webkit-animation: pop_slight 5s ease-in-out infinite;
            animation: pop_slight 5s ease-in-out infinite;
}

.homebutton:hover {
    /* background-color: #ffffff;
    color: #6c1e1e; */
    cursor: pointer;
    -webkit-animation: whiten 20s 1;
            animation: whiten 20s 1;
    
}

@-webkit-keyframes pop_slight {
    0% {
        -webkit-transform: scale(1.025);
                transform: scale(1.025);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1.025);
                transform: scale(1.025);
    }
}

@keyframes pop_slight {
    0% {
        -webkit-transform: scale(1.025);
                transform: scale(1.025);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1.025);
                transform: scale(1.025);
    }
}

@-webkit-keyframes whiten {
    0% {
        background-color: #6c1e1e;
    }
    2.5% {
        background-color: #ffffff;
        color: #6c1e1e;
    }
    100% {
        background-color: #6c1e1e;
        color: #ffffff;
    }
}

@keyframes whiten {
    0% {
        background-color: #6c1e1e;
    }
    2.5% {
        background-color: #ffffff;
        color: #6c1e1e;
    }
    100% {
        background-color: #6c1e1e;
        color: #ffffff;
    }
}

/* Admin Panel */

@-webkit-keyframes pop_out {
    0% {
        visibility: hidden;
    }
    25% {
        visibility: hidden;
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    100% {
        visibility: visible;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@keyframes pop_out {
    0% {
        visibility: hidden;
    }
    25% {
        visibility: hidden;
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    100% {
        visibility: visible;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@-webkit-keyframes go_out {
    0% {
        visibility: visible;
    }
    100% {
        /* transform: translateY(-500px); */
        bottom: -200px;
        visibility:hidden;

    }
}

@keyframes go_out {
    0% {
        visibility: visible;
    }
    100% {
        /* transform: translateY(-500px); */
        bottom: -200px;
        visibility:hidden;

    }
}

#adminpanel{
    background-color: #6c1e1e;
    color: #ffffff;
    bottom: 0;
    height: 60px;
    width: 100%;
    position: fixed;
    overflow: hidden;
    padding: 15px;
    /* border-radius: 20px; */

    -webkit-filter: drop-shadow(4px 4px 10px #cecdcd);

            filter: drop-shadow(4px 4px 10px #cecdcd);
    font-family: Roboto Slab;
    outline: 8px double #F9F9F9;
    display: block;
    visibility: hidden;
    /* animation: pop_out 5s 1; */
}

.admin_button {
    height: 50px;
    width: 50px;
    margin-top: 5px;
    margin-left: 50px;
    /* margin-right: 100px; */
    -webkit-filter: drop-shadow(1px 1px 3px #cecdcd);
            filter: drop-shadow(1px 1px 3px #cecdcd);
}

.admin_button:hover {
    cursor: pointer;
    -webkit-animation: pop 2s ease-in-out infinite;
            animation: pop 2s ease-in-out infinite;
}

panelbig {
    -webkit-filter: drop-shadow(5px 5px 10px #0f0909);
            filter: drop-shadow(5px 5px 10px #0f0909);
    font-size: 45px;
    font-weight: 500;
    color: #ffffff;
    vertical-align: top;
    margin-right: 25px;
}

paneltext {
    margin-left: 10px;
    color: rgb(255, 255, 255);
    font-size: 15px;
    -webkit-filter: drop-shadow(1px 1px 3px #cecdcd);
            filter: drop-shadow(1px 1px 3px #cecdcd);
    text-align: center;
}

/* In Development Waiver */
#indevelopmentwaiver {
    font-family: Roboto Slab;
    outline: 2px maroon solid;
    background-color: #cecdcd;
    color: maroon;
    font-size: 15px;
    margin: auto;
    margin-top: 80px;
    padding: 30px;
    width: 50%;
}
