/* QueryTest Styles */

.querycontainer {
    /* Make the input and textarea fields centered in the middle of the page.
    Make sure they have a grey background with the Roboto Slab text
    Make sure the width of the input field and the height are of decent size
    Center the div's elements in the middle of the page */
    
    /* Center the div's elements in the middle of the page */
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;

    height: 100%;
    width: 30%;
    background-color: #cecdcd;
    padding: 20px;
    border-radius: 20px;
    -webkit-filter: drop-shadow(4px 4px 10px #cecdcd);
            filter: drop-shadow(4px 4px 10px #cecdcd);
    outline: 8px double #F9F9F9;
    margin-bottom: 40px;
}

#queryinput {
    /* Contained in querycontainer class */
    /* Make sure the input box has the Roboto Slab font, is of decent size, long enough for a query
    to be input */
    font-family: Roboto Slab;
    font-size: 15px;
    width: 100%;
    height: 50px;
    border-radius: 20px;
    padding: 10px;
    outline: 8px double #F9F9F9;
    -webkit-filter: drop-shadow(4px 4px 10px #cecdcd);
            filter: drop-shadow(4px 4px 10px #cecdcd);

    margin-right: 30px;
}

#queryresults {
    /* Contained in querycontainer class */
    /* Make sure the text area box has the Roboto Slab font, is of decent size, long enough for a query
    result to be displayed*/
    font-family: Roboto Slab;
    font-size: 18px;
    width: 100%;
    height: 100px;
    border-radius: 20px;
    padding: 10px;
    outline: 8px double #F9F9F9;
    -webkit-filter: drop-shadow(4px 4px 10px #cecdcd);
            filter: drop-shadow(4px 4px 10px #cecdcd);

}

.maptext {
    /*center text, make sure it is the Roboto Slab font, make sure it is of decent size*/
    font-family: Roboto Slab;
    font-size: 20px;
    text-align: center;
    color:#4d0e0e;
    /* outline: 8px double #F9F9F9; */
    -webkit-filter: drop-shadow(4px 4px 10px #cecdcd);
            filter: drop-shadow(4px 4px 10px #cecdcd);

}

.mapcontainer {
    /*center all maps*/
    
    margin: 0 auto;
    margin-bottom: 600px;

}

#map3 {
    /* code for centering a <Map> element*/
    margin-bottom: 100px;
}
/* AuthenticateStyle.css */
/* @author: @rishisant */
/* @desc: Contains all styling used for HSS Application Authentication Side. */

/* All Font Imports */ /* Roboto Slab */

/* Background Image */
body {
    background-image: url(/static/media/subway_photo.c003aea1.png);
    background-size: cover;
    /* background-size: contain; */
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
}
/* Basic Styles */
.icon {
    width: 50px;
    height: 50px;
    margin: 0 auto;
    margin-bottom: 10px;
}

bt {
    font-family: 'Roboto Slab', serif;
    font-size: 20px;
    font-weight: 500;
    color: #000000;
}

ht {
    font-family: 'Roboto Slab', serif;
    font-size: 30px;
    font-weight: 500;
    color: #000000;
}

/* Home Screen Elements */

#mainlogo {
    display: flex;
    justify-content: center;
    width: 450px;
    margin: auto;
    -webkit-filter: drop-shadow(8px 8px 15px #cecdcd);
            filter: drop-shadow(8px 8px 15px #cecdcd);
    margin-top: 10%;
    -webkit-animation: pop 5s ease-in-out infinite;
            animation: pop 5s ease-in-out infinite;
    margin-bottom: 75px;
}

@-webkit-keyframes pop {
    0% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
}

@keyframes pop {
    0% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
}

.homebutton {
    font-family: Roboto Slab;
    outline: 8px double #F9F9F9;
    background-color: #6c1e1e;
    color: #ffffff;
    font-size: 20px;
    padding: 10px;
    border-radius: 20px;
    text-align: center;
    margin-left: 40%;
    margin-right: 40%;
    margin-bottom: 30px;
    -webkit-filter: drop-shadow(4px 4px 10px #cecdcd);
            filter: drop-shadow(4px 4px 10px #cecdcd);
    -webkit-animation: pop_slight 5s ease-in-out infinite;
            animation: pop_slight 5s ease-in-out infinite;
}

.homebutton:hover {
    /* background-color: #ffffff;
    color: #6c1e1e; */
    cursor: pointer;
    -webkit-animation: whiten 20s 1;
            animation: whiten 20s 1;
    
}

@-webkit-keyframes pop_slight {
    0% {
        -webkit-transform: scale(1.025);
                transform: scale(1.025);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1.025);
                transform: scale(1.025);
    }
}

@keyframes pop_slight {
    0% {
        -webkit-transform: scale(1.025);
                transform: scale(1.025);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1.025);
                transform: scale(1.025);
    }
}

@-webkit-keyframes whiten {
    0% {
        background-color: #6c1e1e;
    }
    2.5% {
        background-color: #ffffff;
        color: #6c1e1e;
    }
    100% {
        background-color: #6c1e1e;
        color: #ffffff;
    }
}

@keyframes whiten {
    0% {
        background-color: #6c1e1e;
    }
    2.5% {
        background-color: #ffffff;
        color: #6c1e1e;
    }
    100% {
        background-color: #6c1e1e;
        color: #ffffff;
    }
}

/* Admin Panel */

@-webkit-keyframes pop_out {
    0% {
        visibility: hidden;
    }
    25% {
        visibility: hidden;
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    100% {
        visibility: visible;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@keyframes pop_out {
    0% {
        visibility: hidden;
    }
    25% {
        visibility: hidden;
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    100% {
        visibility: visible;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@-webkit-keyframes go_out {
    0% {
        visibility: visible;
    }
    100% {
        /* transform: translateY(-500px); */
        bottom: -200px;
        visibility:hidden;

    }
}

@keyframes go_out {
    0% {
        visibility: visible;
    }
    100% {
        /* transform: translateY(-500px); */
        bottom: -200px;
        visibility:hidden;

    }
}

#adminpanel{
    background-color: #6c1e1e;
    color: #ffffff;
    bottom: 0;
    height: 60px;
    width: 100%;
    position: fixed;
    overflow: hidden;
    padding: 15px;
    /* border-radius: 20px; */

    -webkit-filter: drop-shadow(4px 4px 10px #cecdcd);

            filter: drop-shadow(4px 4px 10px #cecdcd);
    font-family: Roboto Slab;
    outline: 8px double #F9F9F9;
    display: block;
    visibility: hidden;
    /* animation: pop_out 5s 1; */
}

.admin_button {
    height: 50px;
    width: 50px;
    margin-top: 5px;
    margin-left: 50px;
    /* margin-right: 100px; */
    -webkit-filter: drop-shadow(1px 1px 3px #cecdcd);
            filter: drop-shadow(1px 1px 3px #cecdcd);
}

.admin_button:hover {
    cursor: pointer;
    -webkit-animation: pop 2s ease-in-out infinite;
            animation: pop 2s ease-in-out infinite;
}

panelbig {
    -webkit-filter: drop-shadow(5px 5px 10px #0f0909);
            filter: drop-shadow(5px 5px 10px #0f0909);
    font-size: 45px;
    font-weight: 500;
    color: #ffffff;
    vertical-align: top;
    margin-right: 25px;
}

paneltext {
    margin-left: 10px;
    color: rgb(255, 255, 255);
    font-size: 15px;
    -webkit-filter: drop-shadow(1px 1px 3px #cecdcd);
            filter: drop-shadow(1px 1px 3px #cecdcd);
    text-align: center;
}

/* Authentication Styles */
.authfield {
    font-family: Roboto Slab;
    outline: 5px double #F9F9F9;
    background-color: #4b1111;
    color: #ffffff;
    font-size: 18px;
    padding: 5px;
    border-radius: 20px;
    text-align: center;
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
    -webkit-filter: drop-shadow(4px 4px 10px #cecdcd);
            filter: drop-shadow(4px 4px 10px #cecdcd);
    -webkit-animation: pop_slight 5s ease-in-out infinite;
            animation: pop_slight 5s ease-in-out infinite;
}

#authspacer {
    margin-bottom: 40px;
}

#authcontainer {
    display: block;
}
.table_s {
    font-size: 1.2em;
    font-family: 'Roboto Slab', serif;
    border: 1px solid black;
    background-color: #f2f2f2;
    color: #6c1e1e;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    margin: 0 auto;
}

.table_s th {
    font-size: 1.2em;
    padding: 10px;
    border: 1px solid black;
    color: #6c1e1e;
}
.table_s td {
    font-size: .8 em;
    padding: 5px;
    border: 1px solid black;
    color: #6c1e1e;
} 
.table-layout {
    text-align: center;
    border: 1px solid black;
    border-collapse: collapse;
    font-family:"Trebuchet MS";
    margin: 0 auto 0;
} 
.table-layout td, .table-layout th {
    border: 1px solid grey;
    padding: 5px 5px 0;
}
.table-layout td {
    text-align: left;
}
.selected {
    color: red;
}
tr:hover{
    background-color: #00000027;
    font: white;
}

